.navbar{
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    height: 50px;
    border-bottom: 1px solid lightgray;
    position: sticky;
    top: 0; 
    background-color: white;

    .left{
        display: flex;
        align-items: center;
        gap: 30px;

        span{
            font-weight: bold;
            font-size: 20px;
            color: navy;
        }
        .search{
            display: flex;
            align-items: center;
            gap: 10px;
            border: 1px solid lightgray;
            border-radius: 5px;
            padding: 5px;
            input{
                border: none;
                width: 500px;
            }
        }

    }

    .right{
        display: flex;
        align-items: center;
        gap: 20px;
        white-space: nowrap; /* Prevents elements from stacking */
        overflow: hidden;

        .user{
            display: flex;
            align-items: center;
            gap: 10px; 
            font-weight: 500;
            
            img{
                width: 30px;
                height: 30px;
                border-radius: 50%;
                object-fit: cover;
            }
            span{

            }
        }

    }

}